/* DEFINITIONS */
$pink-color: rgb(237, 21, 255);
$blue-color: rgb(0, 74, 227);
$green-color: rgb(21, 209, 0);
$rect-width: 36vw; 
$rect-height: 27vw; // set to vw temporary for easy testing Should be vh§
/* final position for rects in middle from top and right */
$rect-width-tablet: 555px; // because the media screen is small we set welcome rects to big and stacked
$rect-width-tablet-static: 641px; // static page contains text and should fill a large portion of the screen
$rect-height-tablet: 419px; // in order to stack we set height to small. vw instead of vh because scaling issues else hardcode
$rect-height-tablet-static: 666px;
$top-middle:  250px;
$right-middle: 10vw;
$left-middle: 10vw;
$top-middle-tablet:  20vh;
$right-middle-tablet: 20vw;
$left-middle-tablet: 20vw;

$blue-zindex: 60;
$blue-raster-zindex: 50;

$green-zindex: 40;
$green-raster-zindex: 30;

$pink-zindex: 20;
$pink-raster-zindex: 10;

/* Blue final in welcome position */
$blue-top-welcome-final: 45vh;
$blue-right-welcome-final: 25vw;
$blue-left-welcome-final: calc(100vw - (#{$rect-width} + #{$blue-right-welcome-final}));
/* Blue final in welcome position (TABLET) */
$blue-top-welcome-final-tablet: 63vh; // same calc as pink with start of 44;
$blue-right-welcome-final-tablet: 43vw;
$blue-left-welcome-final-tablet: 14vw;

/* Pink final in welcome position */
$pink-top-welcome-final: 15vh;
$pink-left-welcome-final: 7vw;
/* Pink final in welcome position (TABLET) */
$pink-top-welcome-final-tablet: 12vh; // calculated 15 + (height of rect) 34 - (overlapping) 5 = 44;
$pink-left-welcome-final-tablet: 5vw;

/* Green final in welcome position */
$green-top-welcome-final: 0vh;
$green-right-welcome-final: 5vw;
$green-left-welcome-final: calc(100vw - (#{$rect-width} + #{$green-right-welcome-final}));
/* Green final in welcome position (TABLET) */
$green-top-welcome-final-tablet: 37vh; // give some space to menu
$green-right-welcome-final-tablet: 5vw;
$green-left-welcome-final-tablet: 40vw;

// Raster pos
// RASTER WELCOME PLACES //
$raster-offset: 25px;
//BLUE raster//
$blue-raster-top-welcome-final-tablet: calc(#{$blue-top-welcome-final-tablet} + #{$raster-offset});
$blue-raster-left-welcome-final-tablet: calc(#{$blue-left-welcome-final-tablet} + #{$raster-offset});

//PINK raster//
$pink-raster-top-welcome-final-tablet: calc(#{$pink-top-welcome-final-tablet} + #{$raster-offset});
$pink-raster-left-welcome-final-tablet: calc(#{$pink-left-welcome-final-tablet} + #{$raster-offset});

//GREEN raster//
$green-raster-top-welcome-final-tablet: calc(#{$green-top-welcome-final-tablet} + #{$raster-offset});
$green-raster-left-welcome-final-tablet: calc(#{$green-left-welcome-final-tablet} + #{$raster-offset});


//animations
$slow-welcome-anim: all 700ms;
$fast-exit-anim: all 350ms;


// TABLET SETTINGS
// #######################################//
// #######################################//
// #########       TABLET         ########//
// #######################################//
// #######################################//
@media screen and (max-width: 1000px) {

    .rect-anim {
        position: absolute;
        width: $rect-width-tablet;
        height: $rect-height-tablet;
    }

    .green {
        position: absolute;
        background-color: $green-color;
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
    }
    /*--------------*/
    /* Welcome animation*/
    .green-transition-enter,
    .green-transition-appear {
        top: -100vh;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
    }
    .green-transition-enter-active,
    .green-transition-appear-active {
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
        transition: $slow-welcome-anim;
    }
    .green-transition-exit {
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
    }
    .green-transition-exit-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $green-zindex;
        transition: $slow-welcome-anim;
    }
    
    /* Gather green on top*/
    .green-transition-ontop-enter,
    .green-transition-ontop-appear {
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;

    }
    .green-transition-ontop-enter-done,
    .green-transition-ontop-appear-done {
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: 100;
        transition: $fast-exit-anim;
    }
    
    /* Gather green under*/
    .green-transition-under-enter,
    .green-transition-under-appear {
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-zindex;
        background-color: $green-color;
    }
    .green-transition-under-enter-done,
    .green-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $green-color;
        z-index: -3;
        transition: $fast-exit-anim;
    }
    
    /*--------------*/
    /* Pink rect */
    .pink {
        position: absolute;
        background-color: $pink-color;
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: $pink-zindex;
    }
    .pink-transition-enter,
    .pink-transition-appear {
        left: -47vw;
        z-index: $pink-zindex;
    }
    .pink-transition-enter-active,
    .pink-transition-appear-active {
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
        z-index: $pink-zindex;
    }
    .pink-transition-exit {
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: $pink-zindex;
    }
    .pink-transition-exit-active {
        top: $top-middle-tablet;
        left: $right-middle-tablet;
        transition: $slow-welcome-anim;
    }
    /* Gather pink on top*/
    .pink-transition-ontop-enter,
    .pink-transition-ontop-appear {
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        z-index: $pink-zindex;
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        height: $rect-height-tablet;
    }
    .pink-transition-ontop-enter-done,
    .pink-transition-ontop-appear-done {
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
        z-index: 100;
    }
    
    /* Gather pink under*/
    .pink-transition-under-enter,
    .pink-transition-under-appear {
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        background-color: $pink-color;
        z-index: $pink-zindex;
    }
    .pink-transition-under-enter-done,
    .pink-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $pink-color;
        transition: $fast-exit-anim;
        z-index: $pink-zindex;
    }
    
    /*--------------*/
    /* Blue rect */
    .blue {
        position: absolute;
        background-color: $blue-color;
        top: $blue-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: $blue-zindex;
    }
    .blue-middle {
        background-color: $blue-color;
        top: $top-middle-tablet;
        right: $right-middle-tablet;
    }
    .blue-transition-enter,
    .blue-transition-appear {
        top: 100vh;
        left: -47vw;
    }
    .blue-transition-enter-active,
    .blue-transition-appear-active {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
    }
    .blue-transition-enter-done,
    .blue-transition-appear-done {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
    }
    .blue-transition-exit {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
    
    }
    .blue-transition-exit-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
    
        transition: $slow-welcome-anim;
    }
    .blue-transition-exit-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
    }
    
    /* Gather blue on top*/
    .blue-transition-ontop-enter,
    .blue-transition-ontop-appear {
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
    }
    .blue-transition-ontop-enter-active,
    .blue-transition-ontop-appear-active {
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
    }

    .blue-transition-ontop-enter-done,
    .blue-transition-ontop-appear-done {
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
    }
    
    /* Gather blue under*/
    .blue-transition-under-enter,
    .blue-transition-under-appear {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
    }
    .blue-transition-under-enter-done,
    .blue-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        transition: $fast-exit-anim;
    }

    /*------------*/
    /* Static Rectangles */
    .static-blue-rect {
        position: absolute;
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $blue-color;
        z-index: 99;
    }

    .static-pink-rect {
        position: absolute;
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $pink-color;
        z-index: 99;
    }
    .static-green-rect {
        position: absolute;
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $green-color;
        z-index: 99;
    }

    .static-page {
        padding: 15% 5%;
    }

    .information-text {
        position: relative;
        font-size: 21px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: 600;
        color: white;
        text-align: left;
        z-index: 100;
        ///padding: 25% 10%;
    }
    #text-row {
        position: relative;
        min-height: 400px;
    }

    .img-wrapper {
        width: 100%;
        height: 100%;
    }

    .display {
        background-color: whitesmoke;
        position: absolute;
        top: 0;
        left: 0;
    }

    .static-page-link{
        color: white !important; 
        text-align: left;
        font-size: 21px;
        font-weight: 600;
        z-index: 100;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        border-bottom: solid 2px white;
        border-radius: 0px;
    }
    .icon-pic{
        width: 150px;
        height: auto;
    }
/*--------------*/
/* Button styles */

    .btn {
        position: relative;
        background-color: transparent;
        border: none;
        font-size: 1.7vw;
        font-family: Verdana;
        font-weight: 600;
        padding: 5px;
        margin-right: 1vw;
        color: white;
        :active{
            border: none;
            border-radius: 0px;
        }
        :hover{
            border: none;
        }
        z-index: 100;
        :focus{
        outline: 0 !important;
        }
    }
    .button-blue {
        z-index: 100;
        :focus{
        outline: 0 !important;
        }
    }
    .button-pink {
        position: relative;
        background-color: transparent;
        border: none;
        font-family: Verdana;
        font-weight: 600;
        padding: 5px;
        margin-right: 50px;
        color: white;
        :focus{
            outline:0;
        }
        z-index: 100;
    }
    .button-green {
        position: relative;
        background-color: transparent;
        border: none;
        font-family: Verdana;
        font-weight: 600;
        padding: 5px;
        margin-right: 50px;
        color: white;
        z-index: 100;
    }
    .active {
        border-radius: 0px;
        border-bottom: solid 4px white;
    }


//*****************************//
//*****************************//
//***** Raster Animation ******//
//*****************************//
//*****************************//

    .raster {
        position: absolute;
        width: $rect-width-tablet;
        height: $rect-height-tablet;
        //top: 5vh;
        //right: -5vh;
        z-index: -3;
    }
    .raster-static {
        position: absolute;
        width: $rect-width-tablet;
        height: $rect-height-tablet;
        // top: 2vh;
        // right: -2vh;
    }
    // raster for welcome screen
    .raster-green {
        position: absolute;
        top: $green-raster-top-welcome-final-tablet;
        left: $green-raster-left-welcome-final-tablet;
        width: $rect-width-tablet;
        height: $rect-height-tablet;
        z-index: $green-raster-zindex;
    }
    .raster-blue {
        position: absolute;
        top: $blue-raster-top-welcome-final-tablet;
        left: $blue-raster-left-welcome-final-tablet;
        width: $rect-width-tablet;
        height: $rect-height-tablet;
        z-index: $blue-raster-zindex;
    }
    .raster-pink {
        position: absolute;
        top: $pink-raster-top-welcome-final-tablet;
        left: $pink-raster-left-welcome-final-tablet;
        width: $rect-width-tablet;
        height: $rect-height-tablet;
        z-index: $pink-raster-zindex;
    }
    // raster for static final read screen
    .raster-static-green {
        position: absolute;
        top: calc(#{$top-middle-tablet} + #{$raster-offset});
        left: calc(#{$left-middle-tablet} + #{$raster-offset});
        width: $rect-width-tablet-static;
        height: $rect-height-tablet-static;
        z-index: $green-raster-zindex;
    }
    .raster-static-blue {
        position: absolute;
        top: calc(#{$top-middle-tablet} + #{$raster-offset});
        left: calc(#{$left-middle-tablet} + #{$raster-offset});
        width: $rect-width-tablet-static;
        height: $rect-height-tablet-static;
        z-index: $blue-raster-zindex;
    }
    .raster-static-pink {
        position: absolute;
        top: calc(#{$top-middle-tablet} + #{$raster-offset});
        left: calc(#{$left-middle-tablet} + #{$raster-offset});
        width: $rect-width-tablet-static;
        height: $rect-height-tablet-static;
        z-index: $pink-raster-zindex;
    }
    /* Welcome animation*/
    .green-raster-transition-enter,
    .green-raster-transition-appear {
        top: -100vh;
        z-index: $green-raster-zindex;
    }
    .green-raster-transition-enter-active,
    .green-raster-transition-appear-active {
        top: $green-top-welcome-final-tablet;
        transition: $slow-welcome-anim;
        z-index: $green-raster-zindex;
    }
    .green-raster-transition-exit {
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-raster-zindex;
    }
    .green-raster-transition-exit-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $green-raster-zindex;
        transition: $slow-welcome-anim;
    }
    .green-raster-transition-ontop-enter,
    .green-raster-transition-ontop-appear {
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $green-top-welcome-final-tablet;
        left: $green-left-welcome-final-tablet;
        z-index: $green-raster-zindex;
    }
    .green-raster-transition-ontop-enter-active,
    .green-raster-transition-ontop-appear-active {
        height: $rect-height-tablet;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $green-raster-zindex;
        transition: $fast-exit-anim;
    }
    .green-raster-transition-ontop-enter-done,
    .green-raster-transition-ontop-appear-done {
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: calc(#{$top-middle-tablet} + #{$raster-offset});
        left: calc(#{$left-middle-tablet} + #{$raster-offset});
        z-index: $green-raster-zindex;
    }

    /* Gather green under*/
    .green-raster-transition-under-enter,
    .green-raster-transition-under-appear {
        top: $green-raster-top-welcome-final-tablet;
        left: $green-raster-left-welcome-final-tablet;
        background-color: $green-color;
        z-index: $green-raster-zindex;
    }
    .green-raster-transition-under-enter-done,
    .green-raster-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        background-color: $green-color;
        z-index: $green-raster-zindex;
        transition: $fast-exit-anim;
    }


    // Blue
    .blue-raster-transition-enter,
    .blue-raster-transition-appear {
        top: 100vh;
        left: -47vw;
        z-index: $blue-raster-zindex;
    }
    .blue-raster-transition-enter-active,
    .blue-raster-transition-appear-active  {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
        z-index: $blue-raster-zindex;
        transition: $slow-welcome-anim;
    }
    .blue-raster-transition-exit {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
    }
    .blue-raster-transition-exit-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $blue-raster-zindex;
        transition: $slow-welcome-anim;
    }
    .blue-raster-transition-ontop-enter,
    .blue-raster-transition-ontop-appear {
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
        z-index: $blue-raster-zindex;
    }
    .blue-raster-transition-ontop-enter-active,
    .blue-raster-transition-ontop-appear-active {
        height: $rect-height-tablet;
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $blue-raster-zindex;
        transition: $fast-exit-anim;
    }
    .blue-raster-transition-ontop-enter-done,
    .blue-raster-transition-ontop-appear-done {
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: calc(#{$top-middle-tablet} + #{$raster-offset});
        left: calc(#{$left-middle-tablet} + #{$raster-offset});
        z-index: $blue-raster-zindex;
    }

    /* Gather blue under*/
    .blue-raster-transition-under-enter,
    .blue-raster-transition-under-appear {
        top: $blue-top-welcome-final-tablet;
        left: $blue-left-welcome-final-tablet;
        z-index: $blue-raster-zindex;
    }
    .blue-raster-transition-under-enter-done,
    .blue-raster-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $blue-raster-zindex;
        transition: $fast-exit-anim;
    }
    

    //Pink raster
    .pink-raster-transition-enter,
    .pink-raster-transition-appear {
        top: $pink-raster-top-welcome-final-tablet;
        left: -47vw;
        z-index: $pink-raster-zindex;
    }
    .pink-raster-transition-enter-active,
    .pink-raster-transition-appear-active {
        top: $pink-raster-top-welcome-final-tablet;
        left: $pink-raster-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
        z-index: $pink-raster-zindex;
    }
    .pink-raster-transition-enter-done,
    .pink-raster-transition-appear-done {
        top: $pink-raster-top-welcome-final-tablet;
        left: $pink-raster-left-welcome-final-tablet;
        transition: $slow-welcome-anim;
        z-index: $pink-raster-zindex;
}
    .pink-raster-transition-exit {
        top: $pink-raster-top-welcome-final-tablet;
        left: $pink-raster-left-welcome-final-tablet;
        z-index: $pink-raster-zindex;
    }
    .pink-raster-transition-exit-active {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $pink-raster-zindex;
        transition: $slow-welcome-anim;
    }
    .pink-raster-transition-ontop-enter,
    .pink-raster-transition-ontop-appear {
        //Startingpoint of animation when exiting welcomescreen
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $pink-top-welcome-final-tablet;
        left: $pink-left-welcome-final-tablet;
        z-index: $pink-raster-zindex;
    }
    .pink-raster-transition-ontop-enter-active,
    .pink-raster-transition-ontop-appear-active {
        //endpoint of animation when exiting welcomescreen (this class has the transition prop)
        height: $rect-height-tablet;
        width: $rect-width-tablet;
        top: $pink-raster-top-welcome-final-tablet;
        left: $pink-raster-left-welcome-final-tablet;
        z-index: 80;
        transition: $fast-exit-anim;
    }
    .pink-raster-transition-ontop-enter-done,
    .pink-raster-transition-ontop-appear-done {
        //Static final place for element
        height: $rect-height-tablet-static;
        width: $rect-width-tablet-static;
        top: calc(#{$top-middle-tablet} + #{$raster-offset});
        left: calc(#{$left-middle-tablet} + #{$raster-offset});
        z-index: 80;
        transition: $fast-exit-anim;
    }

    /* Gather pink under*/
    .pink-raster-transition-under-enter,
    .pink-raster-transition-under-appear {
        top: $pink-raster-top-welcome-final-tablet;
        left: $pink-raster-left-welcome-final-tablet;
        z-index: $pink-raster-zindex;
    }
    .pink-raster-transition-under-enter-done,
    .pink-raster-transition-under-appear-done {
        top: $top-middle-tablet;
        left: $left-middle-tablet;
        z-index: $pink-raster-zindex;
        transition: $fast-exit-anim;
    }
}